@charset "UTF-8";
.dk-image-editor {
  position: relative;
  width: 100%;
  height: 100%;
  background: #4d4d4d;
  display: flex;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
}
.dk-image-editor__header {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  color: white;
  z-index: 1000;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}
.dk-image-editor__header-left, .dk-image-editor__header-right {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.dk-image-editor__action-btn, .dk-image-editor__save-btn, .dk-image-editor__confirm-btn {
  padding: 4px 8px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  font-size: 14px;
}
.dk-image-editor__action-btn {
  background: transparent;
  color: white;
}
.dk-image-editor__action-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}
.dk-image-editor__save-btn {
  background: #007bff;
  color: white;
}
.dk-image-editor__save-btn:hover {
  background: #0056b3;
}
.dk-image-editor__confirm-btn {
  background: #28a745;
  color: white;
}
.dk-image-editor__confirm-btn:hover {
  background: #1e7e34;
}
.dk-image-editor__output-size {
  display: flex;
  align-items: center;
  gap: 4px;
  position: relative;
}
.dk-image-editor__output-size input[type=number] {
  width: 60px;
  padding: 4px 6px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 12px;
  text-align: center;
}
.dk-image-editor__output-size input[type=number]:focus {
  outline: none;
  border-color: #007bff;
}
.dk-image-editor__output-size span {
  color: rgba(255, 255, 255, 0.8);
  font-size: 12px;
}
.dk-image-editor__lock-btn {
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  font-size: 12px;
  padding: 4px 6px;
  border-radius: 4px;
  transition: all 0.2s;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 32px;
}
.dk-image-editor__lock-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.4);
  color: white;
}
.dk-image-editor__lock-btn.locked {
  background: rgba(0, 123, 255, 0.2);
  border-color: #007bff;
  color: #007bff;
}
.dk-image-editor__lock-btn.locked:hover {
  background: rgba(0, 123, 255, 0.3);
}
.dk-image-editor__main {
  flex: 1;
  display: flex;
  overflow: auto;
  position: relative;
}
.dk-image-editor__sidebar {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 80px;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
  gap: 0.5rem;
  overflow-y: auto;
  max-height: 70vh;
  z-index: 1000;
