  display: flex;
  align-items: center;
  justify-content: center;
  background: #222;
  overflow: auto;
  min-height: 0;
  position: relative;
}
.dk-image-editor__loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
