}
.dk-image-editor__image-canvas {
  width: 100%;
  height: 100%;
  cursor: default;
  border-radius: 4px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  display: block;
}
.dk-image-editor__image-canvas--select {
  cursor: default;
}
.dk-image-editor__image-canvas--crop {
  cursor: crosshair;
}
.dk-image-editor__image-canvas--annotate {
