/* 设置盒子样式 */
.css-loading {
  position: relative;
  background-color: rgba(0, 0, 2, 0);
  min-height: 60px;
}

.css-loading::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: rgba(255, 255, 255, 0.2509803922);
  z-index: 1;
}

/* 创建转圈的loading动画 */
.css-loading::after {
  content: "";
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
  border: 3px solid #f0f4f7;
  /* 边框颜色 */
  border-top: 3px solid #007bff;
  /* 转圈颜色 */
  border-radius: 50%;
  width: 30px;
  /* 转圈的大小 */
  height: 30px;
  /* 转圈的大小 */
  animation: spin 1s linear infinite;
  /* 动画名称、时间、循环方式 */
  z-index: 2;
}

/* 动画效果：让转圈不断旋转 */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}