.dk-image-editor__filter-preview--apply {
  .dk-image-editor__filter-icon {
    background: linear-gradient(135deg, #fffbeb 0%, #fef3c7 100%);
  }
}
.dk-image-editor__filter-preview--toggle {
  .dk-image-editor__filter-icon {
    background: linear-gradient(135deg, #f0fdf4 0%, #dcfce7 100%);
  }
}

.dk-filter-badge {
  color: #f59e0b;
  font-weight: 700;
  margin-left: 2px;
  font-size: 12px;
}

.dk-filter-count {
  display: inline-block;
  background: #007bff;
  color: white;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  font-size: 10px;
  font-weight: bold;
  text-align: center;
  line-height: 16px;
  margin-left: 4px;
}

.dk-image-editor__spinner {
  width: 20px;
  height: 20px;
  border: 2px solid #f3f3f3;
  border-top: 2px solid #4f46e5;
  border-radius: 50%;
  animation: dk-spin 1s linear infinite;
}

@keyframes dk-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
